@import url(https://fonts.googleapis.com/css?family=Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {

  display: flex;
  background-color: orangered;
  /* flex: 1 1 0; */
  /* height: 10vh; */
  /* flex: 1; */
  /* width: 25%; */
  height: 15%;
}

.logo {
  display: flex;
  background-color: rgb(170, 163, 163);
  width: 50%;
}

.username-input {
  display: flex;
  background-color: #0AFF89;
  width: 50%;
}


.logo-written{
  font-family: 'Rubik', sans-serif;
  background-color: #f1f1f1;
  flex: 1 1;
  font-size: 5rem;
  letter-spacing: -0.1em;
  padding-top: 8px;

}

.logo-image{
  background-color: #d3d3d3;
  /* flex: 0.3 1; */
  /* min-width: 90px; */

}
     
.username {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding-top: .6rem;
  padding-left: 2.5rem;
  /* background-color: rebeccapurple; */
}
input {
  border: 2px solid #000000;
  padding: 5px;
  border-radius: 5px;
  max-width: 80%;
  height: 38px;
}
.btn-add-user {
  width: 50%;
  background-color: rgb(43, 226, 134);
}

.btn-reset-app {
  width: 50%;
  background-color: orangered;
  
}

.input-label {
  /* background-color: #3498db; */
  font-size: 1.3rem;
  text-align: left;
}

@media only screen and (max-width: 750px) {
  .header {
    height: 20%;
  }

  .logo, .username-input {
    width: 100%;
    height: 50%;
  }

  .username {
    width: 80%;
  }

  .btn-add-user, .btn-reset-app {
    width: 20%;
  }

}
.calculations {
  background-color: blueviolet;
  /* flex: 1; */
  /* width: 25%; */
  /* height: 50%; */
  width: 50%;
  display: flex;
}

@media only screen and (max-width: 750px) {
  .calculations {
    height: 60%;
    width: 100%;
  }
}

.client-list {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  width: 40%;
  background-color: rgb(0, 23, 128);
}

.client-name {
  color: white;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  text-decoration: underline
} 

.product-list {
  width: 60%;
  background-color: rgb(0, 109, 128);
  overflow-y: scroll;
}

.product {
  display: flex;
  background-color: brown;
  height: 50px;
  border-bottom: 1px solid white
}

.product-name {
  background-color: greenyellow;
  width: 75%;

}

.product-count {
  background-color: violet;
  width: 25%;
}
.results{
  background-color: green;
  display: flex;
  flex-direction: column;
  /* height: 20%; */
  /* flex: 1; */
  /* width: 25%; */
  width: 50%;

}

.summary {
  background-color: tomato;
  height: 75%;
  display: flex;
  flex-wrap: wrap;


}

.btn-container {
  background-color: tan;
  height: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.summary-detail {
  background-color: skyblue;
  border: .3px solid gray;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 750px) {
  .results {
    height: 40%;
    width: 100%;
  }
}
.footer{
  background-color: aqua;
  /* flex: 1; */
  /* width: 25%; */
  height: 7%;


}

@media only screen and (max-width: 750px) {
  .footer {
    height: 5%;
  }
}
/*USING CSS VARIABLES*/
::root{
  --color-primary: #adf32d;
}


.App {
  /* text-align: center; */
  /* background-color: var(--color-primary); */
  /* max-height: 10vh; */


  /*let's treat this as container*/
  display: flex; 
  flex-direction: column;
  height: 85vh; 
  /* flex-wrap: wrap; */
  max-width: 70vw;
  margin: 0 auto;
  margin-top: 50px;
  background-color: #DFDFDF;
  box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.25);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.middle {
  display: flex;
  height: 78%;
  flex-direction: row-reverse;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  background-color: rgb(247, 247, 247);
}


@media only screen and (max-width: 750px) {
  .header {
    flex-direction: column;
    /* flex: .27; */
  }

  .App {
    height: 100vh; 
    /* flex-wrap: wrap; */
    max-width: 100vw;
    margin: 0 auto;

    background-color: #DFDFDF;
    
  }

  .middle {
    display: flex;
    height: 75%;
    flex-direction: column;
  }
}

.calc {
  background-color: #acd1c2;
  /* flex: 1 */
}



.box {
  width: 33.33333333%;
}

