.calculations {
  background-color: blueviolet;
  /* flex: 1; */
  /* width: 25%; */
  /* height: 50%; */
  width: 50%;
  display: flex;
}

@media only screen and (max-width: 750px) {
  .calculations {
    height: 60%;
    width: 100%;
  }
}

.client-list {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  width: 40%;
  background-color: rgb(0, 23, 128);
}

.client-name {
  color: white;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  text-decoration: underline
} 

.product-list {
  width: 60%;
  background-color: rgb(0, 109, 128);
  overflow-y: scroll;
}

.product {
  display: flex;
  background-color: brown;
  height: 50px;
  border-bottom: 1px solid white
}

.product-name {
  background-color: greenyellow;
  width: 75%;

}

.product-count {
  background-color: violet;
  width: 25%;
}