.header {

  display: flex;
  background-color: orangered;
  /* flex: 1 1 0; */
  /* height: 10vh; */
  /* flex: 1; */
  /* width: 25%; */
  height: 15%;
}

.logo {
  display: flex;
  background-color: rgb(170, 163, 163);
  width: 50%;
}

.username-input {
  display: flex;
  background-color: #0AFF89;
  width: 50%;
}


.logo-written{
  font-family: 'Rubik', sans-serif;
  background-color: #f1f1f1;
  flex: 1;
  font-size: 5rem;
  letter-spacing: -0.1em;
  padding-top: 8px;

}

.logo-image{
  background-color: #d3d3d3;
  /* flex: 0.3 1; */
  /* min-width: 90px; */

}
     
.username {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding-top: .6rem;
  padding-left: 2.5rem;
  /* background-color: rebeccapurple; */
}
input {
  border: 2px solid #000000;
  padding: 5px;
  border-radius: 5px;
  max-width: 80%;
  height: 38px;
}
.btn-add-user {
  width: 50%;
  background-color: rgb(43, 226, 134);
}

.btn-reset-app {
  width: 50%;
  background-color: orangered;
  
}

.input-label {
  /* background-color: #3498db; */
  font-size: 1.3rem;
  text-align: left;
}

@media only screen and (max-width: 750px) {
  .header {
    height: 20%;
  }

  .logo, .username-input {
    width: 100%;
    height: 50%;
  }

  .username {
    width: 80%;
  }

  .btn-add-user, .btn-reset-app {
    width: 20%;
  }

}