.results{
  background-color: green;
  display: flex;
  flex-direction: column;
  /* height: 20%; */
  /* flex: 1; */
  /* width: 25%; */
  width: 50%;

}

.summary {
  background-color: tomato;
  height: 75%;
  display: flex;
  flex-wrap: wrap;


}

.btn-container {
  background-color: tan;
  height: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.summary-detail {
  background-color: skyblue;
  border: .3px solid gray;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 750px) {
  .results {
    height: 40%;
    width: 100%;
  }
}